.search-form .search-button {
  font-size: 24px;
  background-color: #fff;
  transition: 0.2s;
  padding: 5px 15px;
}
.search-form .search-button:hover {
  color: #5ea828;
}

.search-column-categories-container a {
  padding: 10px 20px;
  border-radius: 5px;
}
.search-column-categories-container .active {
  background-color: rgba(220, 225, 255, 0.5);
}

.social-icon-primary {
  font-size: 27px;
  color: #2b40b4;
  transition: 0.2s;
}
.social-icon-primary:hover {
  color: #5ea828;
}

.wpml-ls-current-language span {
  color: #5ea828;
  transition: 0.2s;
}
.wpml-ls-current-language span:hover {
  color: #5ea828;
}

.home_page_section_1 h1 {
  font-size: 64px;
}

.about-us-card {
  line-height: 1.2em;
  font-weight: 600;
  font-size: 14px;
}

.show-btn, .hide-btn {
  font-size: 14px;
}

.consent-link {
  text-decoration: underline;
  color: #787878;
}